import React from 'react';
import { AppBar, Toolbar, styled } from '@mui/material';

const Container = styled(AppBar)`
background:#060606;
height:9vh;
`

const Header = () => {

    const logo = 'https://img.favpng.com/4/23/25/product-design-logo-line-font-angle-png-favpng-zS3m7aLKYzUrGqtSG8QC1QTdy.jpg';

  return (
    <>
      <Container position='static'>
        <Toolbar>
            <img src={logo} alt="logo" style={{width: 40}} />
        </Toolbar>
      </Container>
    </>
  )
}

export default Header;
